<template>
  <div id="error">
    <div v-if="errorType == 'error-token'">
      <ErrorToken />
    </div>
    <div v-else>
      <ErrorNotFound />
    </div>
  </div>
</template>

<script>
import ErrorToken from "../components/error/Token";
import ErrorNotFound from "../components/error/NotFound";
export default {
  name: "pageError",
  components: {
    ErrorToken,
    ErrorNotFound,
  },
  data() {
    return {
      errorType: false,
    };
  },
  mounted() {
    const { msg } = this.$route.params;
    this.errorType = msg;
  },
};
</script>

<style lang=scss scoped>
@import "~@/assets/scss/_variables.scss";

#error {
  font-family: $poppins;
  height: 100vh;
}
</style>